@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #011c40;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 90%;
  max-width: 1160px;
  margin: 0 auto;
}
@media (max-width: 1023px) {
  .container {
    max-width: 678px;
  }
}
@media (max-width: 767px) {
  .container {
    padding-left: 0;
    padding-right: 0;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.page {
  height: 100%;
}

.page__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.main-header {
  position: absolute;
  margin-top: 35px;
  width: 100%;
  z-index: 5;
}

.main-header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.main-header__logo {
  fill: #f9fbfd;
}

.main-header__toggle {
  display: none;
}

@media (max-width: 1023px) {
  .main-header {
    margin-top: 28px;
  }
  .main-header__logo svg {
    margin-top: 2px;
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .main-header {
    margin-top: 0;
  }
  .no-js .main-header {
    position: static;
  }
  .main-header__wrapper {
    padding-top: 20px;
    align-items: flex-start;
    margin-left: 15px;
    background-color: transparent;
    transition: margin-left 500ms ease, box-shadow 500ms ease, background-color 500ms step-start;
  }
  .main-header__wrapper.is-opened,
.no-js .main-header__wrapper {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    height: 100vh;
    width: auto;
    margin: 0;
    padding-top: 20px;
    padding-left: 15px;
    padding-right: 30px;
    margin-left: 35px;
    background-color: #f9fbfd;
    color: #011c40;
    box-shadow: -35px 0 0 rgba(0, 43, 102, 0.5);
    overflow-y: auto;
  }
  .no-js .main-header__wrapper {
    margin-left: 0;
    min-height: min-content;
    height: max-content;
  }
  .main-header__logo {
    margin-bottom: 27px;
  }
  .no-js .main-header__wrapper .main-header__logo {
    fill: #011c40;
  }
  .main-header__wrapper.is-opened .main-header__logo {
    fill: #011c40;
  }
  .main-header__toggle {
    position: absolute;
    top: 24px;
    right: 15px;
    z-index: 2;
    display: block;
    width: 24px;
    height: 14px;
    padding: 0;
    background-color: rgba(255, 255, 255, 0);
    cursor: pointer;
    border: none;
  }
  .main-header__wrapper.is-closed .main-header__toggle::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    width: 24px;
    height: 2px;
    background-color: #ffffff;
    box-shadow: 0 6px 0 0 #ffffff, 0 12px 0 0 #ffffff;
  }
  .main-header__wrapper.is-opened .main-header__toggle {
    top: 23px;
    right: 19px;
  }
  .main-header__wrapper.is-opened .main-header__toggle::before, .main-header__wrapper.is-opened .main-header__toggle::after {
    content: "";
    position: absolute;
    width: 18px;
    height: 2px;
    background-color: #000000;
  }
  .main-header__wrapper.is-opened .main-header__toggle::before {
    transform: rotate(45deg);
    box-shadow: none;
  }
  .main-header__wrapper.is-opened .main-header__toggle::after {
    transform: rotate(-45deg);
  }
  .main-header__wrapper.is-closed .main-header__nav {
    display: none;
  }
  .main-header__wrapper.is-opened .main-header__nav {
    display: block;
  }
}
.main-nav__list {
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
  align-items: baseline;
  margin: 0;
  margin-top: -5px;
  list-style: none;
}

.main-nav__item {
  margin-left: 34px;
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.25));
}
.main-nav__item:first-child {
  margin-right: 0;
}

.main-nav__link {
  position: relative;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #f9fbfd;
  padding-bottom: 5px;
  user-select: none;
}
.main-nav__link::after {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 0%;
  content: ".";
  color: transparent;
  background: #f9fbfd;
  height: 1px;
  transition: 0.3s ease;
}
.main-nav__link:hover {
  color: #c6dff6;
}
.main-nav__link:hover::after {
  width: 100%;
  background: #c6dff6;
}
.main-nav__link:focus, .main-nav__link:focus-within {
  color: #c6dff6;
  outline: none;
}
.main-nav__link:focus::after, .main-nav__link:focus-within::after {
  width: 100%;
  background: #c6dff6;
}
.main-nav__link:active {
  color: rgba(198, 223, 246, 0.3);
}

@media (max-width: 1023px) {
  .main-nav__link {
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
  }
  .main-nav__item {
    margin-left: 24px;
  }
  .main-nav__item:first-child {
    margin-left: 0;
  }
}
@media (max-width: 767px) {
  .main-nav__list {
    margin-top: 0;
    padding-left: 0;
  }
  .main-nav__item {
    width: 100%;
    margin-left: 0;
    margin-bottom: 10px;
    filter: none;
  }
  .main-nav__link {
    display: block;
    width: 100%;
    padding-top: 10px;
    padding-bottom: 8px;
    padding-left: 10px;
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    color: #011c40;
  }
  .main-nav__link:hover {
    color: rgba(1, 28, 64, 0.7);
  }
  .main-nav__link:hover::after {
    background-color: rgba(1, 28, 64, 0.7);
  }
  .main-nav__link:focus,
.main-nav__link:focus-within {
    color: rgba(1, 28, 64, 0.7);
  }
  .main-nav__link:focus::after,
.main-nav__link:focus-within::after {
    background-color: rgba(1, 28, 64, 0.7);
  }
  .main-nav__link:active {
    color: rgba(1, 28, 64, 0.5);
  }
  .main-nav__link:active::after {
    background-color: rgba(1, 28, 64, 0.5);
  }
}
.main-container {
  flex-grow: 1;
}

