.main-footer {
  background-color: #011c40;
}

.main-footer__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 50px;
  padding-bottom: 50px;
}

.main-footer__nav-wrapper {
  display: flex;
  align-items: center;
}

.main-footer__logo {
  min-width: 70px;
  margin-right: 40px;
}

.main-footer__nav-list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
}

.main-footer__nav-item {
  margin-right: 33px;
}
.main-footer__nav-item:last-child {
  margin-right: 0;
}

.main-footer__nav-link {
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #f9fbfd;
  background-image: linear-gradient(to right, #c6dff6, #c6dff6 50%, #f9fbfd 50%);
  user-select: none;
  background-size: 200% 100%;
  background-position: -100%;
  display: inline-block;
  vertical-align: auto;
  padding: 5px 0;
  position: relative;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  transition: all 0.3s ease-in-out;
}

.main-footer__nav-link::before {
  content: "";
  background: #c6dff6;
  display: block;
  position: absolute;
  bottom: -1px;
  left: 0;
  width: 0;
  height: 1px;
  transition: all 0.3s ease-in-out;
}

.main-footer__nav-link:hover,
.main-footer__nav-link:focus {
  background-position: 0;
  outline: none;
}

.main-footer__nav-link:hover::before,
.main-footer__nav-link:focus::before {
  width: 100%;
}

.main-footer__nav-link:active,
.main-footer__nav-link:active::before {
  opacity: 0.7;
}

@media (max-width: 1023px) {
  .main-footer__wrapper {
    padding-top: 48px;
    padding-bottom: 46px;
  }
  .main-footer__logo img {
    width: 38px;
    height: 20px;
  }
  .main-footer__logo {
    min-width: 38px;
    margin-right: 25px;
  }
  .main-footer__nav-item {
    margin-right: 23px;
  }
  .main-footer__nav-item:last-child {
    margin-right: 0;
  }
  .main-footer__nav-link {
    font-size: 12px;
    line-height: 15px;
  }
}
@media (max-width: 767px) {
  .main-footer__wrapper {
    flex-direction: column;
    align-items: flex-start;
    padding-top: 40px;
    padding-bottom: 33px;
  }
  .main-footer__nav-wrapper {
    flex-direction: column;
  }
  .main-footer__nav-list {
    flex-direction: column;
    margin-bottom: 15px;
  }
  .main-footer__nav-item {
    margin-right: 0;
    margin-bottom: 6px;
  }
  .main-footer__nav-item:last-child {
    margin-right: 0;
  }
  .main-footer__logo img {
    width: 42px;
    height: 22px;
  }
  .main-footer__logo {
    margin: 0;
    margin-bottom: 20px;
    align-self: start;
  }
}
.socials {
  display: grid;
  grid-template-columns: repeat(3, min-content);
  gap: 27px;
  margin: 0;
  padding: 0;
  padding-top: 10px;
  list-style: none;
}

.socials__link svg {
  fill: #f9fbfd;
  box-shadow: 0 0 40px 40px #011c40 inset, 0 0 0 0 #011c40;
  transition: all 0.3s ease;
}

.socials__link:focus,
.socials__link:focus-within {
  outline: none;
}

.socials__link:hover svg,
.socials__link:focus svg,
.socials__link:focus-within svg {
  outline: none;
  background-color: #f9fbfd;
  fill: #011c40;
  box-shadow: 0 0 1px 0 #f9fbfd inset, 0 0 1px 4px #f9fbfd;
}

.socials__link:active svg {
  background-color: rgba(198, 223, 246, 0.3);
  box-shadow: 0 0 1px 0 rgba(198, 223, 246, 0.3) inset, 0 0 1px 4px rgba(198, 223, 246, 0.3);
}

@media (max-width: 1023px) {
  .socials {
    margin-left: -5px;
    padding-top: 0;
    gap: 22px;
  }
  .socials__link svg {
    width: 16px;
    height: 16px;
  }
}
